function pullCostValues(){
    const discElement=document.getElementById("monthlyDisc");
    window.sessionStorage.setItem('disc', discElement.value);
    const grocElement=document.getElementById("monthlyGroc");
    window.sessionStorage.setItem('groc', grocElement.value);
    const retirElement=document.getElementById("monthlyRetir");
    window.sessionStorage.setItem('retir', retirElement.value);
    console.log(`Storage Page Discretionary is ${window.sessionStorage.getItem('disc')}`);
    console.log(`Storage Page Grocery is ${window.sessionStorage.getItem('groc')}`);
    console.log(`Storage Page Retirement is ${window.sessionStorage.getItem('retir')}`);
    const nokElement=document.getElementById("kidNumber");
    window.sessionStorage.setItem('NOK', nokElement.value);
    console.log(`Storage Page NOK is ${window.sessionStorage.getItem('NOK')}`);
    const numberOfKids=parseInt(nokElement.value, 10);
    let kidArray=[];
    for(let i=0; i<numberOfKids; i++){
        let j=i+1;
        kidArray[i] = [document.getElementById('kidYear' + j).valueAsNumber, document.getElementById('dayCare' + j).checked, document.getElementById('privCollege' + j).checked];
    }
    window.sessionStorage.setItem('kidArray', JSON.stringify(kidArray));
    console.log(`Storage Page kid array is ${window.sessionStorage.getItem('kidArray')}`);
    const kidElement=document.getElementById("monthlyKid");
    window.sessionStorage.setItem('monthlyKid', kidElement.value);
    console.log(`Storage Page monthly kid is ${window.sessionStorage.getItem('monthlyKid')}`);
    const dayCareElement=document.getElementById("dayCare");
    window.sessionStorage.setItem('dayCare', dayCareElement.value);
    console.log(`Storage Page day care is ${window.sessionStorage.getItem('dayCare')}`);
    const privElement=document.getElementById("privCollege");
    window.sessionStorage.setItem('privCollege', privElement.value);
    console.log(`Storage Page annual private college is ${window.sessionStorage.getItem('privCollege')}`);
    const pubElement=document.getElementById("pubCollege");
    window.sessionStorage.setItem('pubCollege', pubElement.value);
    console.log(`Storage Page annual public is ${window.sessionStorage.getItem('pubCollege')}`);
    window.sessionStorage.setItem('costStored', 1);
}
const pullCostButton3 = document.getElementById("saveCost");
pullCostButton3.addEventListener("click", pullCostValues);

